'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Textarea } from '@/components/ui/textarea'
import { Separator } from '@/components/ui/separator'
import { Badge } from '@/components/ui/badge'
import { Loader2, Globe, Languages } from 'lucide-react'

export default function HomePage() {
  const [url, setUrl] = useState('')
  const [originalText, setOriginalText] = useState('')
  const [translatedText, setTranslatedText] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [step, setStep] = useState<'input' | 'fetched' | 'translated'>('input')

  const handleFetchContent = async () => {
    if (!url.trim()) {
      setError('Vui lòng nhập URL truyện manhwa')
      return
    }

    setLoading(true)
    setError('')
    
    try {
      const response = await fetch('/api/fetch', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ url: url.trim() })
      })
      
      const data = await response.json()
      
      if (response.ok) {
        setOriginalText(data.text)
        setStep('fetched')
      } else {
        setError(data.error || 'Không thể lấy nội dung truyện')
      }
    } catch (err) {
      setError('Lỗi kết nối. Vui lòng thử lại.')
    } finally {
      setLoading(false)
    }
  }

  const handleTranslate = async () => {
    if (!originalText.trim()) {
      setError('Không có nội dung để dịch')
      return
    }

    setLoading(true)
    setError('')
    
    try {
      const response = await fetch('/api/translate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text: originalText })
      })
      
      const data = await response.json()
      
      if (response.ok) {
        setTranslatedText(data.translation)
        setStep('translated')
      } else {
        setError(data.error || 'Không thể dịch nội dung')
      }
    } catch (err) {
      setError('Lỗi dịch thuật. Vui lòng thử lại.')
    } finally {
      setLoading(false)
    }
  }

  const handleReset = () => {
    setUrl('')
    setOriginalText('')
    setTranslatedText('')
    setError('')
    setStep('input')
  }

  return (
    <div className="container mx-auto px-4 py-8 max-w-6xl">
      {/* Header */}
      <div className="text-center mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">
          IsManga
        </h1>
        <p className="text-lg text-gray-600">
          Dịch truyện manhwa từ Naver Comic sang tiếng Việt
        </p>
      </div>

      {/* URL Input Section */}
      <Card className="mb-6">
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Globe className="w-5 h-5" />
            Nhập URL Truyện
          </CardTitle>
          <CardDescription>
            Dán link truyện manhwa từ comic.naver.com vào ô bên dưới
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex gap-2">
            <Input
              type="url"
              placeholder="https://comic.naver.com/webtoon/detail?titleId=..."
              value={url}
              onChange={(e) => setUrl(e.target.value)}
              className="flex-1"
              disabled={loading}
            />
            <Button 
              onClick={handleFetchContent}
              disabled={loading || !url.trim()}
              className="min-w-[120px]"
            >
              {loading && step === 'input' ? (
                <>
                  <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                  Đang lấy...
                </>
              ) : (
                'Lấy Truyện'
              )}
            </Button>
          </div>
          
          {error && (
            <div className="text-red-600 text-sm bg-red-50 p-3 rounded-md">
              {error}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Content Display */}
      {(originalText || translatedText) && (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Original Content */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                <span>Nội Dung Gốc</span>
                <Badge variant="secondary">Tiếng Hàn</Badge>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <Textarea
                value={originalText}
                readOnly
                className="min-h-[400px] resize-none"
                placeholder="Nội dung gốc sẽ hiển thị ở đây..."
              />
              <div className="mt-4 flex gap-2">
                <Button 
                  onClick={handleTranslate}
                  disabled={loading || !originalText.trim()}
                  className="flex-1"
                >
                  {loading && step === 'fetched' ? (
                    <>
                      <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                      Đang dịch...
                    </>
                  ) : (
                    <>
                      <Languages className="w-4 h-4 mr-2" />
                      Dịch Sang Tiếng Việt
                    </>
                  )}
                </Button>
              </div>
            </CardContent>
          </Card>

          {/* Translated Content */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                <span>Bản Dịch</span>
                <Badge variant="default">Tiếng Việt</Badge>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <Textarea
                value={translatedText}
                readOnly
                className="min-h-[400px] resize-none"
                placeholder="Bản dịch sẽ hiển thị ở đây..."
              />
            </CardContent>
          </Card>
        </div>
      )}

      {/* Reset Button */}
      {(originalText || translatedText) && (
        <div className="mt-6 text-center">
          <Separator className="mb-4" />
          <Button variant="outline" onClick={handleReset}>
            Dịch Truyện Mới
          </Button>
        </div>
      )}

      {/* Footer */}
      <div className="mt-12 text-center text-sm text-gray-500">
        <p>
          Ứng dụng demo - Hỗ trợ dịch truyện manhwa từ Naver Comic
        </p>
      </div>
    </div>
  )
}
